.conf__blocks--inner{
	width: 100%;
	display: flex;
	flex-wrap: wrap;
}

.conf__blocks--item{
	width: 240px;
	height: 140px;
	display: flex;
	align-items: center;
	justify-content: center;
	background: #fff;
	margin: .3rem;
}

.load{
	text-transform: uppercase;
	margin: 2rem 2rem 0 0;
	display: inline-block;
}

.conf__block--img{
	width: auto;
	height: auto;
	max-height: 100%;
	max-width: 100%;
}

// Подвал

.footer{
	padding: 3.5rem 0;
	background: #001c62;
}

.footer__inner{
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.footer__copy{
	text-transform: uppercase;
	color: #fff;
	font-size: 1.4rem;
	text-align: center;
}

.footer__address{
	font-size: 1.4rem;
	text-align: center;
}

.footer__phone{
	font-size: 1.4rem;
	text-align: center;
}

// Партнеры

.partners{
	background: url("../img/bg4.jpg") center no-repeat;
	background-size: cover;
	min-height: 100vh;
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

// Схема работы

.work__inner{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	padding: 5rem 0;
}

.work__carousel--inner{
	margin-top: 4rem;
	width: 100%;
}

.work__carousel--item:focus{
	border: none!important;
	outline: none!important;
}

.work__carousel--item{
	display: flex!important;
	align-items: flex-start;
	width: 30%;
	margin-right: 3rem;
}

.work__carousel--text--inner{
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.work__carousel--num{
	margin-right: 2rem;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 55px;
	height: 55px;
	border-radius: 50%;
	color: #fff;
	background: linear-gradient(271.86deg, #00E3A0 21.45%, #006DE4 100%);
	font-size: 2rem;
	font-family: 'MullerB', sans-serif;
	flex-shrink: 0;
}

.work__carousel--title{
	font-size: 2.6rem;
	font-family: 'MullerB', sans-serif;
}

.work__carousel--text{
	margin-top: 2rem;
}

// Контакты

.map{
	width: 100%;
	display: flex;
}

.map__map{
	min-height: 300px;
	width: 60%;
	flex-shrink: 0;
}

.map__content{
	width: 40%;
	padding: 3rem;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	background: linear-gradient(323.56deg, #0B2261 29.64%, #006DE4 109.9%), #C4C4C4;
}

.contact__cont{
	margin-top: 3.5rem;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	width: 100%;
}

.contact__mail{
	margin-top: 1rem;
}

.contact__address{
	margin-top: 4rem;
}

.contact__text{
	margin-top: 1rem;
}

// Каталог страница

.catalog__inner{
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.catalog__content{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	margin-top: 8rem;
}

.catalog__item--inner{
	width: 100%;
	margin-top: 3rem;
	display: flex;
	flex-wrap: wrap;
}

.katalog__carousel--item.catalog__item{
	width: 22.5%;
	margin: 1.5rem;
}

.katalog__carousel--item:hover{
	background: darken(#0B2261, 5);
}

// Модальное окно карточки товара

.modal__card{
	display: none;
	position: fixed;
	top: 50%;
	z-index: 500;
	left: 50%;
	transform: translate(-50%, -50%);
	padding: 6rem 3rem;
	width: 100%;
	max-width: 450px;
	background: linear-gradient(316.09deg, #0B2261 39.32%, #006DE4 106.92%), #C4C4C4;
}

.modal__card--cross{
	position: absolute;
	top: 15px;
	right: 15px;
	cursor: pointer;
}

.modal__card--content{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.modal__card--img--inner{
	width: 100%;
	height: 290px;
	display: flex;
	align-items: center;
	justify-content: center;
	overflow: hidden;
}

.modal__card--img{
	width: 100%;
	height: auto;
}

.modal__card--title{
	margin-top: 1.5rem;
	font-size: 2.6rem;
	font-family: 'MullerB', sans-serif;
}

.modal__card--text{
	margin-top: 1rem;
}


// Адаптив

@media(max-width: 1720px){
	.container{
		max-width: 1160px;
	}

	// О нас

	.about__carousel--item{
		max-width: 240px;
	}

	.about__item--title{
		font-size: 2.2rem;
	}

	// Каталог страница

	.katalog__carousel--item.catalog__item{
		width: 22.4%;
	}
}

@media(max-width: 1200px){
	.container{
		max-width: 978px;
	}

	// Шапка

	.header__nav--link{
		margin-right: 2rem;
	}

	.main__title{
		font-size: 3.6rem;
	}

	.main__application{
		margin-top: 5rem;
	}

	.chat{
		width: 60px;
		height: 60px;
	}

	.chat:before{
		width: 80px;
		height: 80px;
	}

	// О нас

	.about__content{
		max-width: 240px;
	}

	.about__title{
		font-size: 3.7rem;
	}

	.about__carousel--item{
		max-width: 322px;
	}

	.about__item--title{
		font-size: 2rem;
	}

	.about__carousel--inner .slick-list{
		padding: 0rem 5.5rem!important;
	}

	// Текст

	.text__title{
		font-size: 3.7rem;
	}

	.text__img--inner{
		width: 350px;
	}

	// Нам доверяют

	.conf__title{
		font-size: 3.7rem;
	}

	.conf__text--inner{
		max-width: 290px;
	}

	.conf__blocks--item{
		width: 210px;
		height: 130px;
	}

	.conf__blocks--cont{
		max-width: 648px;
	}

	// Каталог страница

	.katalog__carousel--item.catalog__item{
		width: 30%;
		max-width: 30%;
	}
}

@media(max-width: 998px){
	.container{
		max-width: 748px;
	}

	// Шапка

	.header__inner .header__nav{
		display: none;
	}

	.header__inner .header__button--katalog{
		display: none;
	}

	.mobile__plash .header__nav,
	.mobile__plash .header__button--katalog{
		display: flex;
	}

	.mobile__plash{
		display: none;
	}

	.mobile__menu--icon{
		display: block;
	}

	.main__title{
		font-size: 3.2rem;
	}

	// О нас

	.about__inner{
		flex-direction: column;
		align-items: flex-start;
	}

	.about__content{
		margin-right: 0;
		max-width: 350px;
	}

	.about__title{
		font-size: 3.4rem;
	}

	// Карусель

	.carousel__img--inner{
		right: 40px;
		width: 240px;
	}

	.prev, .next{
		display: none;
	}

	// Текст

	.text__title{
		font-size: 3.2rem;
	}

	.text__img--inner{
		width: 300px;
	}

	// Каталог(блок)

	.katalog__title{
		font-size: 3rem;
	}

	// Подвал

	.footer__inner{
		flex-direction: column;
	}

	.footer__address, .footer__phone{
		margin-top: .5rem;
	}

	// Нам доверяют

	.conf__title{
		font-size: 3.3rem;
	}

	.conf__text--inner{
		max-width: 240px;
	}

	.conf__blocks--item{
		width: 195px;
		height: 105px;
	}

	.conf__blocks--cont{
		max-width: 402px;
	}

	.conf__inner{
		padding: 1rem 0;
	}

	// Каталог страница

	.katalog__carousel--item.catalog__item{
		padding: 1.5rem;
		width: 29.3%;
	}
}

@media(max-width: 768px){
	.container{
		max-width: 460px;
	}

	// Шапка

	.scroll__down{
		display: none;
	}

	.main__title{
		font-size: 2.8rem;
	}

	.main__application{
		margin-top: 3rem;
	}

	.chat{
		bottom: 20px;
		right: 20px;
	}

	.phone{
		display: none;
	}

	.header__phone--icon{
		margin-right: 0;
	}

	.mobile__plash{
		display: none;
	}

	//  О нас

	.about__carousel--inner .slick-list{
		padding: 0!important;
	}

	.about__carousel--item:hover{
		filter: none;
	}

	.about__carousel--item{
		background: #0B2261;
	}

	.about__title{
		font-size: 2.9rem;
	}

	.about__item--text{
		opacity: 1;
	}

	.about__text{
		margin-top: 2rem;
	}

	.about__item--img--inner{
		background: #00E3A0;
	}

	// Модальное окно

	.modal{
		padding: 5rem 2rem;
		max-width: 300px;
	}

	.modal__title{
		font-size: 2.5rem;
	}

	.agree__label{
		font-size: 1.3rem;
	}

	.agree__label:before{
		top: -1px;
	}

	.agree__label:after{
		top: 4px;
	}

	// Карусель

	.carousel__img--inner{
		top: 20px;
		transform: translateY(0);
		right: 50%;
		transform: translateX(50%);
		width: 200px;
	}

	.carousel__title{
		font-size: 2.6rem;
	}

	.carousel__text{
		margin-top: 1rem;
	}

	.main__application.mtm{
		margin-top: 1rem;
		display: flex;
		justify-content: center;
	}

	// Текст

	.text__inner{
		flex-direction: column-reverse;
		align-items: center;
	}

	.text__title{
		font-size: 2.7rem;
		text-align: center;
		margin-top: 2rem;
	}

	.about, .katalog{
		padding: 7rem 0;
	}

	// Каталог(блок)

	.katalog__title{
		font-size: 2.5rem;
	}

	// Нам доверяют

	.conf__inner{
		flex-direction: column;
		align-items: flex-start;
	}

	.conf__text--inner{
		max-width: 100%;
	}

	.conf__blocks--cont{
		margin-top: 2rem;
		max-width: 100%;
	}

	.conf__blocks--item{
		width: 48%;
		height: 120px;
	}

	// Контакты

	.map{
		flex-direction: column-reverse;
		align-items: flex-start;
	}

	.map__map, .map__content{
		width: 100%;
	}

	// Каталог страница

	.katalog__carousel--item.catalog__item{
		padding: 2rem;
		width: 48%;
		margin: 2rem 0rem 0rem 0rem;
		max-width: 48%;
	}

	.catalog__item--inner{
		justify-content: space-between;
	}
}

@media(max-width: 480px){
	.container{
		max-width: 300px;
	}

	// Шапка

	.main__title{
		margin-top: 5rem;
		font-size: 2.5rem;
	}

	// О нас

	.about__content{
		max-width: 100%;
	}

	.about__title{
		font-size: 2.5rem;
	}

	.about__carousel--item{
		max-width: 300px;
	}

	// Карусель

	.trail{
		bottom: 0;
	}

	.slider .box .details{
		transform: translateY(-5rem);
	}

	// Текст

	.text__img--inner{
		width: 100%;
	}

	.text__title{
		font-size: 2.5rem;
		margin-top: 1rem;
	}

	// Каталог(блок)

	.katalog__title{
		font-size: 2.2rem;
	}

	.katalog__carousel--inner{
		margin-top: 0;
	}

	// Нам доверяют

	.conf__blocks--item{
		height: 100px;
	}

	.conf__title{
		font-size: 2.5rem;
	}

	// Схема работы

	.work__carousel--item{
		flex-direction: column;
		align-items: flex-start;
	}

	.work__carousel--text--inner{
		margin-top: 1rem;
	}

	// Каталог страница

	.katalog__carousel--item.catalog__item{
		width: 100%;
		max-width: 100%;
	}

	.catalog__item--inner{
		margin-top: 0;
	}

	.catalog__content{
		margin-top: 4rem;
	}

	// Модальное окно карточки товара

	.modal__card{
		height: 100vh;
		width: 100%;
		max-width: 100%;
	}
}